<template>
  <v-card class="mx-auto mt-5" max-width="1000">
    <v-data-table
      :headers="headersPreferencias"
      :items="
        turnoSelecionado in linhasPreferencias &&
        professorSelecionado in linhasPreferencias[turnoSelecionado]
          ? linhasPreferencias[turnoSelecionado][professorSelecionado]
          : []
      "
      :hide-default-footer="true"
      disable-pagination
      disable-filtering
      disable-sort
      dense
      class="tabela"
    >
      <template v-slot:top>
        <v-toolbar flat dense>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-toolbar-title v-on="on" v-bind="attrs">
                Preferências
              </v-toolbar-title>
            </template>
            <span>Especificar horários preferidos para cada professor</span>
          </v-tooltip>
          <v-icon class="ml-2" @click="dialogDeletePreferences = true"
            >mdi-delete</v-icon
          >
          <v-spacer />
          <v-col cols="2">
            <v-select
              v-if="turnos != null && turnos.length > 1"
              v-model="turnoSelecionado"
              :items="turnos"
              item-text="name"
              item-value="id"
              hide-details
            />
          </v-col>
          <v-col cols="2">
            <v-select
              v-if="professores != null && professores.length > 1"
              v-model="professorSelecionado"
              :items="professores"
              item-text="name"
              item-value="id"
              hide-details
            />
          </v-col>
        </v-toolbar>
      </template>

      <template v-slot:[`item.dia`]="props">
        {{ diasSemana[props.item.dia].name }}
      </template>

      <template
        v-for="(sala, index) in salas"
        v-slot:[`item.${sala.id}`]="props"
      >
        <v-edit-dialog
          :key="index"
          :return-value.sync="props.item[sala.id]"
          ref="editDialog"
        >
          {{ formatPreference(props.item[sala.id]) }}
          <template v-slot:input>
            <v-card flat class="pb-3">
              <v-select
                v-model="props.item[sala.id].tipo"
                :items="tiposPreferencia"
                item-text="text"
                item-value="id"
                label="Preferir"
                @change="handlePreferenceChange($event, props.index, sala.id)"
                append-outer-icon="mdi-delete"
                @click:append-outer="clearPreference(props.index, sala.id)"
              />
              <v-text-field
                v-model="props.item[sala.id].aulas"
                label="Número de aulas"
                type="number"
                step="1"
                min="0"
                :max="aulasPorDiaTurnoSelecionado - 1"
                :disabled="
                  props.item[sala.id].tipo === 4 ||
                  props.item[sala.id].tipo === -1
                "
                @change="handleNumberLecturesChange($event, props.index)"
              />
              <v-row>
                <v-btn
                  v-if="![2, 3].includes(props.item[sala.id].tipo)"
                  color="blue darken-1"
                  text
                  @click="applyToRow(props.index, sala.id)"
                >
                  Aplicar na linha inteira
                </v-btn>
              </v-row>
              <v-row>
                <v-btn
                  v-if="![2, 3].includes(props.item[sala.id].tipo)"
                  color="blue darken-1"
                  text
                  @click="applyToColumn(props.index, sala.id)"
                >
                  Aplicar na coluna inteira
                </v-btn>
              </v-row>
              <v-row>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="applyToDayAllClasses(props.index, sala.id)"
                >
                  Aplicar no dia em todas salas
                </v-btn>
              </v-row>
              <v-row>
                <v-btn color="blue darken-1" text @click="closeEditDialog">
                  Fechar
                </v-btn>
              </v-row>
            </v-card>
          </template>
        </v-edit-dialog>
      </template>
    </v-data-table>
    <v-divider />
    <v-data-table
      :headers="headersSalasTurnoSelecionado"
      :items="
        turnoSelecionado in linhasRestricoes &&
        professorSelecionado in linhasRestricoes[turnoSelecionado]
          ? linhasRestricoes[turnoSelecionado][professorSelecionado]
          : []
      "
      :hide-default-footer="true"
      disable-pagination
      disable-filtering
      disable-sort
      dense
      class="tabela mt-5"
    >
      <template v-slot:top>
        <v-toolbar flat dense>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-toolbar-title v-on="on" v-bind="attrs">
                Restrições
              </v-toolbar-title>
            </template>
            <span
              >Proibir ou evitar aulas de um professor nas salas e horários
              configurados</span
            >
          </v-tooltip>
          <v-icon class="ml-2" @click="dialogDeleteRestrictions = true"
            >mdi-delete</v-icon
          >

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-on="on" v-bind="attrs" class="ml-2">mdi-help</v-icon>
            </template>
            <span
              >Para configurar restrições, clique e arraste com o botão esquerdo
              do mouse na tabela abaixo. Para remover uma restrição, faça o
              mesmo processo com o botão direito.</span
            >
          </v-tooltip>

          <span class="ml-5"> {{ stringTotalAulasProfessorTurno }} </span>
          <v-spacer />
          <v-radio-group v-model="paintMode" hide-details>
            <v-row>
              <v-col>
                <v-radio value="0" label="Proibir" color="#D50000" />
              </v-col>
              <v-col>
                <v-radio value="1" label="Evitar" color="#607D8B" />
              </v-col>
            </v-row>
          </v-radio-group>
        </v-toolbar>
      </template>

      <template v-slot:item="props">
        <tr>
          <td
            v-if="props.item.aula % aulasPorDiaTurnoSelecionado === 0"
            :rowspan="aulasPorDiaTurnoSelecionado"
            class="vertical-text"
          >
            {{
              diasSemana[
                Math.floor(props.item.aula / aulasPorDiaTurnoSelecionado)
              ].name
            }}
          </td>
          <td
            :style="{
              'border-bottom':
                props.item.aula % aulasPorDiaTurnoSelecionado ===
                aulasPorDiaTurnoSelecionado - 1
                  ? '1px solid black !important'
                  : null,
              'text-align': 'center',
            }"
          >
            {{ (props.item.aula % aulasPorDiaTurnoSelecionado) + 1 }}
          </td>
          <td
            :style="{
              'border-bottom':
                props.item.aula % aulasPorDiaTurnoSelecionado ===
                aulasPorDiaTurnoSelecionado - 1
                  ? '1px solid black !important'
                  : null,
              background: getColor(props.item[sala.id]),
            }"
            v-for="(sala, index) in salasTurnoSelecionado"
            :key="index"
            @mousemove="paintCell($event, props.item, sala.id, props.index)"
            @mousedown="mouseDownHandler($event, props.index, sala.id)"
            @mouseup="mouseUpHandler($event, props.index, sala.id)"
            @contextmenu="preventDefault"
          ></td>
        </tr>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDeletePreferences" max-width="500px">
      <v-card>
        <v-card-title class="text-h6">
          Limpar preferências do professor selecionado no turno selecionado?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialogDeletePreferences = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="clearPreferences">OK</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDeleteRestrictions" max-width="500px">
      <v-card>
        <v-card-title class="text-h6">
          Limpar restrições do professor selecionado no turno selecionado?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialogDeleteRestrictions = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="clearRestrictions"
            >OK</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import multipleApplication from "@/mixins/multipleApplication";
import helper from "@/mixins/helper";

export default {
  name: "Restricoes",
  mixins: [multipleApplication, helper],
  props: ["id"],
  data() {
    return {
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: {
        id_professor_1: null,
        id_professor_2: null,
        id_turno_1: null,
        id_turno_2: null,
        dia: null,
      },
      tiposRestricao: [
        { id: -1, text: "Permitir (Padrão)" },
        { id: 0, text: "Proibir" },
        { id: 1, text: "Evitar" },
      ],
      tiposPreferencia: [
        { id: -1, text: "Nenhuma (Padrão)" },
        { id: 4, text: "Forçar última aula" },
        { id: 2, text: "Primeiras aulas" },
        { id: 3, text: "Últimas aulas" },
      ],
      lookup: [
        { icon: "mdi-close-box", class: "proibir" },
        { icon: "mdi-close-box-outline", class: null },
      ],
      linhasRestricoes: {},
      linhasPreferencias: {},
      diasRestricoes: {},
      diasRestricoesHeaders: [
        { text: "Dia", value: "dia" },
        { text: "Móvel", value: "mobile" },
      ],
      vinculosDiasRestricoes: [],
      vinculosDiasRestricoesHeaders: [
        { text: "Dia", value: "dia" },
        { text: "Professor 1", value: "id_professor_1" },
        { text: "Turno 1", value: "id_turno_1" },
        { text: "Professor 2", value: "id_professor_2" },
        { text: "Turno 2", value: "id_turno_2" },
        { text: "Ações", value: "actions" },
      ],
      aulasPorProfessor: {},
      rightMouseDown: 0,
      leftMouseDown: 0,
      startRow: null,
      startCol: null,
      endRow: null,
      endCol: null,
      lastSelection: [],
      paintMode: "0",
      dialogDeletePreferences: false,
      dialogDeleteRestrictions: false,
    };
  },

  mounted() {
    this.load();
  },

  watch: {
    professorSelecionado() {
      this.editedItem = Object.assign({}, this.defaultItem);
    },
    turnoSelecionado() {
      this.editedItem = Object.assign({}, this.defaultItem);
    },
  },

  methods: {
    async load(isReload = false) {
      try {
        this.setLoading(isReload);
        this.saved();
        await this.getStructure();
        const linhasRestricoes = {};
        const diasRestricoesLoad = {};
        const linhasPreferencias = {};
        this.turnos.forEach((turno) => {
          const idTurno = turno.id;
          linhasRestricoes[idTurno] = {};
          diasRestricoesLoad[idTurno] = {};
          linhasPreferencias[idTurno] = {};
          this.professores.forEach((professor) => {
            linhasRestricoes[idTurno][professor.id] = [];
            linhasPreferencias[idTurno][professor.id] = [];
            for (let i = 0; i < turno.classes * this.numero_dias; i++) {
              const linha = { aula: i };
              const salas_turno = this.salas.filter(
                (sala) => sala.turno === idTurno
              );
              salas_turno.forEach((s) => (linha[s.id] = -1));
              linhasRestricoes[idTurno][professor.id].push(linha);
            }
            diasRestricoesLoad[idTurno][professor.id] = [
              { movable: false },
              { movable: false },
              { movable: false },
              { movable: false },
              { movable: false },
            ];

            for (let i = 0; i < this.numero_dias; i++) {
              const linha_preferencias = { dia: i };
              const salas_turno = this.salas.filter(
                (sala) => sala.turno === idTurno
              );
              salas_turno.forEach((s) => {
                linha_preferencias[s.id] = { aulas: 0, tipo: -1 };
              });
              linhasPreferencias[idTurno][professor.id].push(
                linha_preferencias
              );
            }
          });
        });

        const resp = await this.$axios.get("/restricoes/" + this.id);
        const {
          restricoes,
          diasRestricoes,
          vinculosDiasRestricoes,
          preferencias,
        } = resp.data;

        for (let linha of restricoes) {
          const indice = linha.dia * linha.aulas_por_dia + linha.aula;
          linhasRestricoes[linha.id_turno][linha.id_professor][indice][
            linha.id_sala
          ] = linha.tipo;
        }

        for (let diaRestricao of diasRestricoes) {
          const { id_turno, id_professor, movel } = diaRestricao;
          movel.split(",").forEach((value, index) => {
            diasRestricoesLoad[id_turno][id_professor][index].movable = Boolean(
              Number(value)
            );
          });
        }

        for (let linha of preferencias) {
          const { id_turno, id_sala, id_professor, dia, num_aulas, tipo } =
            linha;
          const referenciada =
            linhasPreferencias[id_turno][id_professor][dia][id_sala];
          referenciada.tipo = tipo;
          if (tipo !== 4) {
            referenciada.aulas = num_aulas;
          }
        }

        this.linhasRestricoes = linhasRestricoes;
        this.diasRestricoes = diasRestricoesLoad;
        this.vinculosDiasRestricoes = vinculosDiasRestricoes;
        this.linhasPreferencias = linhasPreferencias;
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },
    async save(reload = true) {
      const {
        linhasRestricoes,
        vinculosDiasRestricoes,
        diasRestricoes,
        linhasPreferencias,
        id,
      } = this;

      const resp = await this.$axios.post("/restricoes", {
        restricoes: linhasRestricoes,
        vinculosDiasRestricoes,
        diasRestricoes,
        linhasPreferencias,
        id,
      });
      if (resp.status === 200 && reload) {
        this.load(true);
      }
    },
    closeEditDialog() {
      this.$refs.editDialog.forEach((dialog) => {
        dialog.isActive = false;
      });
    },
    editItem(item) {
      this.editedIndex = this.vinculosDiasRestricoes.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.vinculosDiasRestricoes.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.vinculosDiasRestricoes.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    saveItem() {
      if (this.validateRestrictionDayLink(this.editedItem)) {
        if (this.editedIndex > -1) {
          this.checkChanges(
            this.vinculosDiasRestricoes[this.editedIndex],
            this.editedItem
          );
          Object.assign(
            this.vinculosDiasRestricoes[this.editedIndex],
            this.editedItem
          );
        } else {
          this.changed();
          this.vinculosDiasRestricoes.push(this.editedItem);
        }
        this.close();
        this.editedItem = Object.assign({}, this.defaultItem);
      } else {
        this.$toast.error(
          "Não é possível vincular restrições de um professor com si mesmo. Isso só é possível se a vinculação for com outro turno."
        );
      }
    },

    validateRestrictionDayLink(link) {
      const { id_professor_1, id_professor_2, id_turno_1, id_turno_2, dia } =
        link;
      if (id_professor_1 === id_professor_2 && id_turno_1 === id_turno_2) {
        return false;
      }
      for (const restrictionDayLink of this.vinculosDiasRestricoes) {
        if (
          dia === restrictionDayLink.dia &&
          ((id_professor_1 === restrictionDayLink.id_professor_1 &&
            id_professor_2 === restrictionDayLink.id_professor_2 &&
            id_turno_1 === restrictionDayLink.id_turno_1 &&
            id_turno_2 === restrictionDayLink.id_turno_2) ||
            (id_professor_1 === restrictionDayLink.id_professor_2 &&
              id_professor_2 === restrictionDayLink.id_professor_1 &&
              id_turno_1 === restrictionDayLink.id_turno_2 &&
              id_turno_2 === restrictionDayLink.id_turno_1))
        ) {
          return false;
        }
      }
      return true;
    },
    clear() {
      this.changed();
      const idProfessor = this.professorSelecionado;
      for (
        let i = 0;
        i < this.linhasRestricoes[this.turnoSelecionado][idProfessor].length;
        i++
      ) {
        for (const sala in this.linhasRestricoes[this.turnoSelecionado][
          idProfessor
        ][i]) {
          if (sala === "aula") {
            continue;
          }

          this.linhasRestricoes[this.turnoSelecionado][idProfessor][i][sala] =
            -1;
        }
      }
    },
    preventDefault(e) {
      e.preventDefault();
    },
    getClassIndex(classId) {
      return this.headersSalasTurnoSelecionado.findIndex(
        (h) => h.value === classId
      );
    },
    getColor(restriction) {
      switch (restriction) {
        case 0:
          return "#D50000";
        case 1:
          return "#607D8B";
        default:
          return null;
      }
    },
    paintCell(e, item, salaId, row) {
      if (this.leftMouseDown || this.rightMouseDown) {
        this.changed();
        this.fillRegion(
          this.startRow,
          row,
          this.getClassIndex(this.startCol),
          this.getClassIndex(salaId)
        );
        return;
      }
    },
    mouseDownHandler(e, row, col) {
      if (e.button === 0) {
        this.leftMouseDown = 1;
      } else {
        this.rightMouseDown = 1;
      }
      this.startRow = row;
      this.startCol = col;
    },

    mouseUpHandler(e, row, col) {
      this.leftMouseDown = 0;
      this.rightMouseDown = 0;
      this.endRow = row;
      this.endCol = col;
      this.lastSelection = [];
    },

    fillRegion(startRow, endRow, startCol, endCol) {
      const startR = Math.min(startRow, endRow);
      const endR = Math.max(startRow, endRow);
      const startC = Math.min(startCol, endCol);
      const endC = Math.max(startCol, endCol);
      this.clearLastSelection();
      this.lastSelection = [];
      for (let i = startR; i <= endR; i++) {
        for (let j = startC; j <= endC; j++) {
          if (
            this.linhasRestricoes[this.turnoSelecionado][
              this.professorSelecionado
            ][i][this.headersSalasTurnoSelecionado[j].value] === -1 &&
            this.leftMouseDown
          ) {
            this.linhasRestricoes[this.turnoSelecionado][
              this.professorSelecionado
            ][i][this.headersSalasTurnoSelecionado[j].value] = Number(
              this.paintMode
            );
            this.lastSelection.push([
              i,
              this.headersSalasTurnoSelecionado[j].value,
            ]);
          } else if (
            this.linhasRestricoes[this.turnoSelecionado][
              this.professorSelecionado
            ][i][this.headersSalasTurnoSelecionado[j].value] ===
              Number(this.paintMode) &&
            this.rightMouseDown
          ) {
            this.lastSelection.push([
              i,
              this.headersSalasTurnoSelecionado[j].value,
              this.linhasRestricoes[this.turnoSelecionado][
                this.professorSelecionado
              ][i][this.headersSalasTurnoSelecionado[j].value],
            ]);
            this.linhasRestricoes[this.turnoSelecionado][
              this.professorSelecionado
            ][i][this.headersSalasTurnoSelecionado[j].value] = -1;
          }
        }
      }
    },

    clearLastSelection() {
      this.lastSelection.forEach((pos) => {
        this.linhasRestricoes[this.turnoSelecionado][this.professorSelecionado][
          pos[0]
        ][pos[1]] = pos[2] || -1;
      });
    },

    applyToRow(indexRow, idSala) {
      this.changed();
      const professor = this.professorSelecionado;
      const value = {
        ...this.linhasPreferencias[this.turnoSelecionado][professor][indexRow][
          idSala
        ],
      };

      for (const sala in this.linhasPreferencias[this.turnoSelecionado][
        professor
      ][indexRow]) {
        if (sala === "dia") {
          continue;
        }

        this.linhasPreferencias[this.turnoSelecionado][professor][indexRow][
          sala
        ] = {
          ...value,
        };

        this.handlePreferenceChange(value, indexRow, sala);
      }
    },
    applyToColumn(indexRow, idSala) {
      this.changed();
      const professor = this.professorSelecionado;
      const value = {
        ...this.linhasPreferencias[this.turnoSelecionado][professor][indexRow][
          idSala
        ],
      };
      for (
        let i = 0;
        i < this.linhasPreferencias[this.turnoSelecionado][professor].length;
        i++
      ) {
        this.linhasPreferencias[this.turnoSelecionado][professor][i][idSala] = {
          ...value,
        };
        this.handlePreferenceChange(value, i, idSala);
      }
    },
    applyToDayAllClasses(indexRow, idSala) {
      this.changed();
      const professor = this.professorSelecionado;
      const value = {
        ...this.linhasPreferencias[this.turnoSelecionado][professor][indexRow][
          idSala
        ],
      };
      for (let i = 0; i < this.numero_dias; i++) {
        for (const sala in this.linhasPreferencias[this.turnoSelecionado][
          professor
        ][indexRow]) {
          if (sala === "dia") {
            continue;
          }

          this.linhasPreferencias[this.turnoSelecionado][professor][i][sala] = {
            ...value,
          };
        }
      }
    },
    handlePreferenceChange(preferenceTypeId, index, classId) {
      //Preferencias de primeiras / últimas aulas precisam ser aplicadas na linha inteira
      if ([2, 3].includes(preferenceTypeId)) {
        this.salasTurnoSelecionado.forEach((sala) => {
          this.linhasPreferencias[this.turnoSelecionado][
            this.professorSelecionado
          ][index][sala.id].tipo = preferenceTypeId;
        });
      } else {
        this.salasTurnoSelecionado.forEach((sala) => {
          if (classId === sala.id) {
            return;
          }

          let tipo =
            this.linhasPreferencias[this.turnoSelecionado][
              this.professorSelecionado
            ][index][sala.id].tipo;
          if ([2, 3].includes(tipo)) {
            this.linhasPreferencias[this.turnoSelecionado][
              this.professorSelecionado
            ][index][sala.id].tipo = -1;
          }
        });
      }
    },
    handleNumberLecturesChange(numberLectures, index) {
      this.salasTurnoSelecionado.forEach((sala) => {
        this.linhasPreferencias[this.turnoSelecionado][
          this.professorSelecionado
        ][index][sala.id].aulas = numberLectures;
      });
    },
    formatPreference(item) {
      const { tipo, aulas } = item;
      if (tipo == null || aulas == null || tipo === -1) {
        return null;
      }
      const descTipoPreferencia = this.preferenciaLookup[tipo];
      if (tipo === 4) {
        return descTipoPreferencia;
      } else if (aulas === 1) {
        if (tipo === 2) {
          return "Primeira aula";
        } else if (tipo === 3) {
          return "Última aula";
        }
      } else if (aulas > 1) {
        return aulas + " " + descTipoPreferencia.toLowerCase();
      }

      return null;
    },
    clearPreferences() {
      this.changed();
      for (
        let i = 0;
        i <
        this.linhasPreferencias[this.turnoSelecionado][
          this.professorSelecionado
        ].length;
        i++
      ) {
        for (let idSala in this.linhasPreferencias[this.turnoSelecionado][
          this.professorSelecionado
        ][i]) {
          if (idSala === "dia") {
            continue;
          }
          this.linhasPreferencias[this.turnoSelecionado][
            this.professorSelecionado
          ][i][idSala] = { aulas: 0, tipo: -1 };
        }
      }
      this.dialogDeletePreferences = false;
    },
    clearRestrictions() {
      this.changed();
      for (
        let i = 0;
        i <
        this.linhasRestricoes[this.turnoSelecionado][this.professorSelecionado]
          .length;
        i++
      ) {
        for (let idSala in this.linhasRestricoes[this.turnoSelecionado][
          this.professorSelecionado
        ][i]) {
          if (idSala === "aula") {
            continue;
          }
          this.linhasRestricoes[this.turnoSelecionado][
            this.professorSelecionado
          ][i][idSala] = -1;
        }
      }
      this.dialogDeleteRestrictions = false;
    },
    clearPreference(indexRow, idSala) {
      console.log("clearPref");
      this.changed();
      this.closeEditDialog();
      this.linhasPreferencias[this.turnoSelecionado][this.professorSelecionado][
        indexRow
      ][idSala].tipo = -1;
      this.linhasPreferencias[this.turnoSelecionado][this.professorSelecionado][
        indexRow
      ][idSala].aulas = 0;
      this.handlePreferenceChange(-1, indexRow, idSala);
      this.handleNumberLecturesChange(0, indexRow, idSala);
    },
  },

  computed: {
    headersPreferencias() {
      return Array({
        text: "Dia",
        value: "dia",
        align: "center",
        width: "120px",
      }).concat(
        Array.from(this.salasTurnoSelecionado, (sala) => {
          return { text: sala.name, value: sala.id };
        })
      );
    },
    preferenciaLookup() {
      const entries = Array.from(this.tiposPreferencia, (pref) => [
        pref.id,
        pref.text,
      ]);
      return Object.fromEntries(entries);
    },
    diasRestricoesSelecionado() {
      if (
        this.diasRestricoes == null ||
        this.turnoSelecionado == null ||
        this.professorSelecionado == null
      ) {
        return [];
      }

      if (!(this.turnoSelecionado in this.diasRestricoes)) {
        return [];
      }

      return this.diasRestricoes[this.turnoSelecionado][
        this.professorSelecionado
      ];
    },
    vinculosDiasRestricoesProfessorSelecionado() {
      return this.vinculosDiasRestricoes.filter((vinculo) =>
        [vinculo.id_professor_1, vinculo.id_professor_2].includes(
          this.professorSelecionado
        )
      );
    },
    formTitle() {
      if (this.professorSelecionado && this.turnoSelecionado) {
        return this.editedIndex === -1
          ? `Vincular restrições de ${
              this.professoresLookup[this.professorSelecionado]
            } (${this.turnosLookup[this.turnoSelecionado]}) a:`
          : "Editar vínculo";
      }
      return null;
    },
    defaultItem() {
      return {
        id_professor_1: this.professorSelecionado,
        id_professor_2: null,
        id_turno_1: this.turnoSelecionado,
        id_turno_2: null,
        dia: null,
      };
    },
  },
};
</script>

