export default {
  methods: {
    applyToRow(indexRow, idSala) {
      this.closeEditDialog();
      this.$emit("applyToRow", this.professorSelecionado, indexRow, idSala);
    },
    applyToColumn(indexRow, idSala) {
      this.closeEditDialog();
      this.$emit("applyToColumn", this.professorSelecionado, indexRow, idSala);
    },
    applyToDay(indexRow, idSala) {
      this.closeEditDialog();
      this.$emit("applyToDay", this.professorSelecionado, indexRow, idSala);
    },
    applyToDayAllClasses(indexRow, idSala) {
      this.closeEditDialog();
      this.$emit(
        "applyToDayAllClasses",
        this.professorSelecionado,
        indexRow,
        idSala
      );
    },
  },
};
